<template>
  <div class="">
    <div class="m-body">
	  	<div class="homeTabbar">
	  		<div class="m-head">
		    	<div class="m-h-btn" @click="geto('play')"><i class="fa fa-bars"></i></div>
		    	<h3 class="m-name">{{ msg }}</h3> 
			    <div class="m-h-btn" @click="geto('search')"><i class="fa fa-search"></i></div>
        </div>
        <div class="m-ctrl">
            <div id="home1" @click="toggleTab(1)" :class="[homeID==1?'m-ctrl-active':'']" >推荐</div>
            <div id="home2" @click="toggleTab(2)" :class="[homeID==2?'m-ctrl-active':'']" >排行</div>
            <div id="home3" @click="toggleTab(3)" :class="[homeID==3?'m-ctrl-active':'']" >歌手</div>
        </div>
	  	</div>
      <div>
        <div class="cellLeaderboard" v-for="(item,index) of arr0" :key="index" @click="goSongList($event)">
          <img :src="item.coverImgUrl" alt="">
          <div class="leaderboardInfo">
            <div class=""  v-for="(item,index) of item.tracks.slice(0,3)"  :key="index">{{index+1}}.{{item.name}}</div>
          </div>
        </div>
      </div>
	  </div>
  </div>
  
</template>

<script>
export default {
  name: 'App',
  mounted (){
    this.getData();
    	      
  },
  data () {
    return {
      msg: 'R MUSIC',
      homeID:2,
      navbarData: "home2",
      PHBarr:[3,0,1,2,6],
      arr0:[],
    }
  },
  methods:{
    getData:function(){
      var that =this
      for (const i of that.PHBarr) {
        var duix = 'that.arr'+i
        // console.log(eval(duix))
        $.ajax({
          type: "GET",
          url: "http://120.79.162.149:3000/top/list?idx="+i,
          data: "",
          success: function (data) {
            var list=data.playlist
            that.arr0.push(list);
           
          }
        });
      }
    },
    goSongList:function(){

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cellLeaderboard{width: 100vw;height: 100px;display: flex;}
.cellLeaderboard img{width: 94px;height: 94px;margin: 3px}
.leaderboardInfo{width: calc(100vw - 100px);text-align: left;display: flex;flex-wrap: wrap;align-items: center;padding: 20px 6px;}
.leaderboardInfo div{height: 20px;overflow: hidden;width: 100%;text-overflow: ellipsis;white-space: nowrap;font-size: 10px;}
</style>



