<template>
	<div id="Mbox">
		<div class="homeTabbar">
	  		<div class="m-head">
		    	<div class="m-h-btn" @click="leftMenu()"><i class="fa fa-bars"></i></div>
		    	<!-- <div class="m-h-btn" @click="geto('play')"><i class="fa fa-bars"></i></div> -->
		    	<h3 class="m-name">{{ msg }}</h3> 
			    <div class="m-h-btn" @click="geto('search')"><i class="fa fa-search"></i></div>
			</div>
			<div class="m-ctrl">
		  		<div id="home1" @click="toggleTab(1)" :class="[homeID==1?'m-ctrl-active':'']" >推荐</div>
		  		<div id="home2" @click="toggleTab(2)" :class="[homeID==2?'m-ctrl-active':'']" >排行</div>
		  		<div id="home3" @click="toggleTab(3)" :class="[homeID==3?'m-ctrl-active':'']" >歌手</div>
			</div>
	  	</div>
		<div class="">
			<div class="swiperBg"></div>
		    <swiper :options="swiperOption">
			    <swiper-slide  v-for="(item,index) of dataList" :key="index">
			    	<img class="lunbo" :src="item.picUrl"/>
			    </swiper-slide>
			    <div class="swiper-pagination"  slot="pagination"></div>
			</swiper>
		</div>
		<div class="m-body">
			<a class="m-title" href="">推荐歌单<i class="fa fa-angle-right fa-2x"></i></a>
			<div class="cellMusic" v-for="(item,index) of recommendList" :key="index"  @click="jumpPage($event)" :id="item.id">
				<img class="mImg" :src="item.picUrl"/>
				<div class="mName">{{item.name}}</div>
			</div>
		</div>
		<div class="music-list" v-if="musicList">
			<div class="ml-head " :class="{headTop:istop}">
				<div class="back" @click="musiclistToggle()"><i class="el-icon-back"></i></div>
				<div class="songListTop">
					<div class="SLT-top">歌单</div>
					<div class="SLT-bot">{{songList.description}}</div>
				</div>
				<div class="Tright-ctrl" @click="musiclistToggle()"><i class="el-icon-search"></i></div>
				<div class="Tright-ctrl" @click="musiclistToggle()"><i class="el-icon-more"></i></div>
			</div>
			<!-- :style="{background-image:songList.coverImgUrl}" -->
			<div class="songList-box" >
				<div class="songListInfo" >
					<div class="SLB-maskBg" v-bind:style="{backgroundImage:'url(' + songList.coverImgUrl + ')'}"></div>
					<div class="songList-bg-box">
						<img class="songList-bg" :src="songList.coverImgUrl"/>
						<div class="songList-bg-icon" style="top: 4px;"><i class="el-icon-search"></i>{{Math.round(songList.playCount/10000)}}万</div>
						<i  class="el-icon-info songList-bg-icon" style="bottom:4px;"></i>
					</div>
					<div class="songListName">
						<div class="SLN-top">{{songList.name}}</div>
						<div class="SLN-bot">
								<img :src="songList.creator.avatarUrl" alt="">
								<div>{{songList.creator.nickname}}<i class="el-icon-arrow-right"></i></div>
						</div>
					</div>
				</div>
				<div class="functionBtn">
					<div class="Tright-ctrl" @click="musiclistToggle()" v-if="ishide"><i class="fa fa-commenting-o"></i></div>
					<div class="Tright-ctrl" @click="musiclistToggle()" v-if="ishide"><i class="el-icon-share"></i></div>
					<div class="Tright-ctrl" @click="musiclistToggle()" v-if="ishide"><i class="el-icon-download"></i></div>
					<div class="Tright-ctrl" @click="musiclistToggle()" v-if="ishide"><i class="el-icon-edit-outline"></i></div>
				</div>
				<div class="song-list" :class="{SLfixed:isfixed}">
					<div class="allPlay">
						<div><i class="fa fa-play-circle-o"></i>播放全部<div class="c-hui">(共{{songList.tracks.length}}首)</div></div>
						<div class="collection"><i class="el-icon-plus"></i>收藏({{songList.subscribedCount}})</div>
					</div>
					<div class="song-list-box">
						<div class="cell-song" v-for="(item,index) of songList.tracks" :key="index" :id="item.id">
							<div class="songindex">{{index+1}}</div>
							<div class="songInfo">
								<div class="songName">{{item.name}}</div>
								<div class="songartist c-hui">{{item.artists[0].name}}</div>
							</div>
							<div class="songindex"><i class="fa fa-ellipsis-v"></i></div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="menuMask" v-if="mask" @click="closeMask"></div>
		<div class="leftMenu" v-if="Menu">
			<div class="">
				<p>登入后手机电脑多端同步，尽享海量高品质音乐</p>
				<el-button @click="geto('loging')" size="mini" round>登入</el-button>
			</div>
		</div>
	</div>
</template>

<script>
import Swiper from 'swiper';
export default {
    name: 'App',
    mounted (){
    	this.getData();
    	var mySwiper = new Swiper ('.swiper-container', {
		    loop: true,
		    // 如果需要分页器
		    pagination: '.swiper-pagination',
			});
			
  		window.addEventListener('scroll', this.handleScroll)   
    },
    data () {
    	return {
    		msg: 'R MUSIC',
      		homeID:1,
 				navbarData: "home2",
    		//url = 'http://musicapi.leanapp.cn',
    		url:'http://120.79.162.149:3000',
      		dataList:[],
      		recommendList:[],
      		songList:[],
      		musicList:false,
      		swiperOption: {
		          pagination:{
		            el:'.swiper-pagination'
		          },
		          autoplay:{
		              delay:6000,
		              disableOnInteraction:false
		          }
			},
			Menu:false,
			mask:false,
			istop:'',
			ishide:true,
			}
    },
    
    methods:{
    	getData:function(){
    		var that =this
    		$.ajax({
			   type: "GET",
			   url: 'http://120.79.162.149:3000/banner',
			   data: "",
			   success: function(data){
			   	that.dataList=data.banners

			   }
				});
				$.ajax({
					type: "GET",
					url: this.url+'/personalized',
					data: "",
					success: function(data){
						that.recommendList=data.result
					}
				});
			},
    	jumpPage:function(e){
    		this.musicList = true;
    		var that =this;
    		var playListId = e.currentTarget.id
			$.ajax({
			   type: "GET",
			   url: this.url+'/playlist/detail?id='+ playListId,
			   data: "",
			   success: function(data){
			   		that.songList=data.result
			   }
			});
			},
			// 退出歌单
    	musiclistToggle:function(){
    		this.musicList =false
				this.songList =[]
				window.removeEventListener('scroll', this.handleScroll)
			},
			leftMenu:function(){
				this.Menu==true? this.Menu=false:this.Menu=true
				this.mask==true? this.mask=false:this.mask=true
			},
			closeMask:function(){
				this.Menu=false
				this.mask=false
			},
			handleScroll:function () {
				var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
				// console.log(scrollTop)
				if(scrollTop>=160){
					this.istop='headTop',
				 	this.isfixed=true
				}else{
					this.istop=''
					this.isfixed=false
				}
				scrollTop>=120?this.ishide=false:this.ishide=true
      }
    }
}
</script>

<style>
.SLfixed{position: fixed;top: 44px;height: calc(100vh - 44px);overflow: auto;}
.headTop{position: fixed;top: 0;}
.collection i{width: 17px !important;}
.collection{height: 100%;line-height: 45px;color:#fff;background-image: linear-gradient(to right, #fe594a 0%, #d43b32 100%);border-radius: 0 12px 0 0 ;padding: 0 10PX;font-size: 12px;}
.songList-bg-box{position: relative;width: 100px;height: 100px;}
.songList-bg-icon{position: absolute;right: 4px;font-size: 10px;}
.menuMask{width: 100vw;height: 100vh;background: #000000;opacity: .4;position: fixed;top: 0;left: 0;z-index: 555;}
.leftMenu{position: fixed;top: 0;left: 0;width: 260px;height: 100vh;background: #f2f3f4;z-index: 600;}
/* ----------------------------------- */
.m-ctrl div{font-size: 14px;}
.m-body{display: flex;flex-wrap: wrap;}
.m-title{width: 40%;margin-right: 60%;height: 36px;font-weight: bold;color: #2C3E50;text-align: left;margin-left: 10px;display: flex;align-items: center;text-decoration: none;}
.m-title>i{margin-left: 10px;}
.lunbo{width: 93vw;border-radius: 4px;}
.cellMusic{width: 33.33vw;height: 43vw;}
.mImg{width: 88.8%;border-radius: 4px;}
.mName{font-size: 12px;padding: 0 4px;height: 35px;overflow: hidden;}
.music-list{position: absolute;top: 0;left: 0;width: 100%;height: 100%;background: #fff;z-index: 500;}
.ml-head{width: 100%;height: 44px;display: flex;align-items: center;position: fixed;top: 0;z-index: 800;}
.back{width: 44px;height: 44px;display: flex;align-items: center;justify-content: center;color: black;}
.song-list{background: #f2f3f4;width: 100vw;border-radius: 12px 12px 0 0 ;}
.song-list-box,.allPlay{display: flex;border-bottom: 1px solid #e4e4e4;}
.allPlay{align-items: center;height: 44px;font-size: 15px;display: flex;justify-content: space-between;}
.allPlay>div:first-child {display: flex;}
.cell-song{width: 100%;height: 44px;display: flex;align-items: center;border-bottom: 1px solid #e4e4e4;}
.songindex,.allPlay i{width: 44px;}
.song-list-box{flex-wrap: wrap;}
.songList-box{z-index: 600;}
.songList-bg{width: 100px;height: 100px;border-radius: 4px;}
.songInfo{width: calc(100vw - 88px);}
.songartist{font-size: 12px;display: flex;}
.songName{font-size: 13px;display: flex;}
#Mbox{width: 100vw;height: 100%;overflow: hidden;}
.swiperBg{width: 100%;height: 100px;background: #e14439;position: absolute;border-top: 1px solid #e14439;}
.swiper-pagination-bullet-active {opacity: 1;background: #fd6c62;}
.Tright-ctrl{width: 44px;height: 44px;display: flex;align-items: center;justify-content: center;color: black;float: right;font-size: 18px;}
.songListInfo{display: flex;padding: 54px 18px 0;color: #fff;}
.songListTop{width: calc(100vw - 132px);text-align: left;display: flex;flex-wrap: wrap;}
.SLT-top{width: 100%;height: 66.66%;}
.SLT-bot{width: 100%;height: 33.33%;font-size: 8px;overflow: hidden;text-overflow: ellipsis;height: 16px;white-space: nowrap;}
.songListName{padding: 10px 20px;width: calc(100% - 136px)}
.SLN-top{text-align: left; font-size: 16px;height: 46px;overflow: hidden;text-overflow: ellipsis;}
.SLN-bot{display: flex;align-items: center;margin: 8px 0;}
.SLN-bot>img{width: 28px;height: 28px;;border-radius: 50px;margin-right: 10px;}
.SLN-bot>div{font-size: 10px; }
.SLB-maskBg{content:'';position:absolute;top:0;left:0;width:100%;height:100vw;filter:blur(90px);z-index:-1;background-size:cover;}
.functionBtn{color: #fff;display: flex;align-items: center;justify-content: space-around;height: 44px;}
.functionBtn div{width: 25%;}
</style>



