<template>
  <div>
      <div class="back" @click="$router.go(-1)"><i class="fa fa-angle-left fa-2x"></i></div>
      <div class="logingBox">
          <div class="logingTitle">登入</div>
          <el-input v-model="userId" placeholder="请输入账户"></el-input>
          <el-input v-model="userPw" placeholder="密码"  type="password"></el-input>
          <el-button class="lBtn" type="danger" @click="loging()" size="small" round>登入</el-button>
      </div>
  </div>
</template>

<script>
export default {
  mounted (){

    },
  data () {
    return {
        userId:'',
        userPw:'',
        LoginMethod:'',
    }
},
  methods:{
      loging:function(){
        //   this.userId==''?
        //   this.userPw==''?
        //   判断登入方式
          this.userId= "^([a-zA-Z0-9_\\-\\.]+)@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.)|(([a-zA-Z0-9\\-]+\\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})$"?this.LoginMethod = '?email=':this.LoginMethod = '/cellphone?phone='

          $.ajax({
			   type: "GET",
			   url: 'http://120.79.162.149:3000/login'+this.LoginMethod+this.userId+'&password='+ this.userPw,
			   data: "",
			   success: function(data){
			   	//  console.log(document.cookie)
                    $router.go(-1)
			   }
			});
            
      }
  }
}
</script>
<style>
.back{color: #252525}
.logingBox{width: 80vw;margin: 60px auto;}
.logingTitle{margin: 16px;}
.el-input{margin-bottom: 10px;}
.lBtn{width: 100%;}
</style>


